import Api from '@/common/api';
import { mapGetters } from 'vuex';
import uniNumberBox from '@/components/uni-number-box.vue';

export default {
	components: {
		uniNumberBox
	},

	data() {
		return {
			total: 0, //总价格
			discount:0,//优惠
			allChecked: false, //全选状态  true|false
			cartList: []
		};
	},
	computed:{
		...mapGetters(['hasLogin','userInfo']),
	empty(){
		return this.cartList.length<=0;
	}},
	onShow: function() {
		if (this.hasLogin) {
			this.loadData();
		}
	},
	onLoad() {
		if (this.hasLogin) {
			this.loadData();
		}
	},
	methods: {
		//请求数据
		async loadData() {
			let params = {};
			let data = await Api.apiCall('get', Api.order.cartList, params);
			if(data.code!=0) return;
			data=data.data;
			let cartList = data.goods.map(item => {
				item.checked = '1';
				data.id_nums.find(v=>{
					let id_num= v.split("-");
					 if(id_num[0]==item.id){
						 	item.quantity=parseInt(id_num[1]);
							return true;
					 }
					 return false
				})
				return item;
			});
			this.cartList = cartList;
			this.calcTotal(); //计算总价
		},
		//监听image加载完成
		onImageLoad(key, index) {
			this.$set(this[key][index], 'loaded', 'loaded');
		},
		//监听image加载失败
		onImageError(key, index) {
			this[key][index].image = '/static/errorImage.jpg';
		},
		navToLogin() {
			uni.navigateTo({
				url: '/pages/public/login'
			});
		},
		//选中状态处理
		check(type, index) {
			if (type === 'item') {
				this.cartList[index].checked = !this.cartList[index].checked;
			} else {
				const checked = !this.allChecked;
				const list = this.cartList;
				list.forEach(item => {
					item.checked = checked;
				});
				this.allChecked = checked;
			}
			this.calcTotal(type);
		},
		//数量
		numberChange(data) {
			this.cartList[data.index].quantity = data.number;
			let params = { id: this.cartList[data.index].id, quantity: data.number };
			data = Api.apiCall('POST', Api.order.updateQuantity, params);
			this.calcTotal();
		},
		//删除
		deleteCartItem(index) {
			let list = this.cartList;
			let row = list[index];
			let id = row.id;
			let params = { id: id };
			let data = Api.apiCall('post', Api.order.deleteCart, params);
			this.cartList.splice(index, 1);
			this.calcTotal();
			uni.hideLoading();
		},
		//清空
		clearCart() {
			uni.showModal({
				content: '清空购物车？',
				success: e => {
					if (e.confirm) {
						let params = {id:'clear'  };
						let data = Api.apiCall('post', Api.order.deleteCart, params);
						this.cartList = [];
					}
				}
			});
		},
		//计算总价
		calcTotal() {
			let list = this.cartList;
			if (list.length === 0) {
				this.empty = true;
				return;
			}
			let total = 0,discount=0;
			let checked = true;
			
			list.forEach(item => {
				if (item.checked === '1') {
					total += item.goodsPrice * item.quantity;
					discount+=(item.goodsOriginalPrice-item.goodsPrice)* item.quantity;
				} else if (checked === true) {
					checked = false;
				}
			});
			this.allChecked = checked;
			this.discount=discount;
			this.total = Number(total.toFixed(2));
		},
		//创建订单
		createOrder() {
			let list = this.cartList;
			var ids = '';
			list.forEach(item => {
				if (item.checked === true || item.checked === '1') {
					ids = item.id + ',' + ids;
				}
			});
			let cartIds = ids.substr(0, ids.length - 1);
			uni.navigateTo({
				url: `/pages/order/createOrder?cartIds=${cartIds}&memberId=${this.userInfo.id}`
			});

			this.$api.msg('跳转下一页 sendData');
		}
	}
};
